.book_view {
  padding-top: 40px;
  background-color: #118286;

  .left {
    figure {
      img {
        width     : 100%;
        height    : 450px;
        // object-fit: cover;
      }
    }

    .button_area {
      margin-top     : 25px;
      display        : flex;
      justify-content: center;

      .download_btn {
        button {
          span {
            svg {
              fill: #fff;
            }
          }
        }
      }
    }
  }

  .right {
    padding-left: 75px;

    .book_desc {
      margin-top: 24px;
      width     : 100%;

      td {
        display: inline-block;
        margin : 4px 0;
        width  : 25%;
      }

      .year {
        width: 10%;
      }

      .lang {
        width: 25%;
      }
    }
  }

}